@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-3{
          .aside{
            &__bestHolo,
            &__newHolo,
            &__threeD,
            &__examples,
            &__custom{
              display: none;
            }
          }
        }
        .col-lg-4{
          .product{
            &__slider{
              .itemSlider{
                li{
                  width: 345px;
                  height: 345px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border: 1px solid #c5c5c5;
                  img{
                    padding: 20px;
                    width: 100%;
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
              }
              .nav{
                display: flex;
                flex-wrap: wrap;
                li{
                  margin: 14px;
                  width: 65.3px;
                  height: 65.3px;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  border: 1px solid #c5c5c5;
                  &:nth-child(4n+1){
                    margin: 14px 14px 14px 0;
                  }
                  img{
                    padding: 5px;
                    width: 100%;
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
                .cycle-pager-active{
                  border-color: #009fff;
                }
              }
            }
            .customize{
              margin: 35px 0;
              width: 90%;
              display: block;
              font: 1em/35px "Arial";
              color: #010101;
              text-align: center;
              text-decoration: none;
              background-color: #cfcfcf;
            }
            .rating{
              ul{
                display: flex;
                align-items: center;
                li{
                  margin: 0 2.5px;
                  i{
                    color: #e7cb73;
                    line-height: 1em;
                    &:hover{
                      cursor: pointer;
                    }
                  }
                  &:nth-child(1){
                    margin: 0 2.5px 0 0;
                  }
                  a{
                    margin: 0 0 0 10px;
                    display: block;
                    font: .75em/1em "Arial";
                    color: #0081ef;
                  }
                }
              }
            }
          }
        }
        .col-lg-5{
          .product{
            &__nav{
              ul{
                display: flex;
                li{
                  a{
                    display: inline-block;
                    font: .875em/40px "Arial";
                    color: #0081ef;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    color: #777;
                    text-align: center;
                    background-color: #eee;
                    border: 1px solid #dfdfdf;
                  }
                  &:nth-child(3){
                    margin: 0 0 0 35px;
                  }
                }
              }
            }
            &__headline{
              margin: 25px 0 35px;
              p{
                font: 1.25em/1.375em "Arial";
                font-weight: bold;
              }
            }
            &__options{
              .size{
                margin: 7px 0;
                display: flex;
                p{
                  font: .875em/1.5em "Arial";
                  font-weight: bold;
                  color: #525252;
                }
                ul{
                  margin: 0 0 0 15px;
                  li{
                    display: flex;
                    align-items: center;
                    input[type="radio"]{
                      display: none;
                    }
                    input[type="radio"] + span:before{
                      font-family: "FontAwesome";
                    }
                    input[type="radio"]:checked + span:before{
                      content: "\f00c";
                      font-size: 10px;
                      line-height: 20px;
                      color: #6d6d6d;
                    }
                    label{
                      width: 15px;
                      height: 15px;
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      background-color: #fff;
                      border: 1px solid #d0d0d0;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                    span{
                      font: .875em/1.5em "Arial";
                    }
                  }
                }
              }
              .shape,
              .baseColor,
              .serialNumber{
                margin: 7px 0;
                display: flex;
                p{
                  font: .875em/1.5em "Arial";
                  font-weight: bold;
                  color: #525252;
                }
                ul{
                  display: flex;
                  li{
                    margin: 0 0 0 15px;
                    display: flex;
                    align-items: center;
                    input[type="radio"]{
                      display: none;
                    }
                    input[type="radio"] + span:before{
                      font-family: "FontAwesome";
                    }
                    input[type="radio"]:checked + span:before{
                      content: "\f00c";
                      font-size: 10px;
                      line-height: 20px;
                      color: #6d6d6d;
                    }
                    label{
                      width: 15px;
                      height: 15px;
                      display: flex;
                      justify-content: center;
                      align-items: center;
                      background-color: #fff;
                      border: 1px solid #d0d0d0;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                    span{
                      font: .875em/1.5em "Arial";
                    }
                  }
                }
              }
            }
            &__quantity{
              margin: 40px 0 70px;
              width: 370px;
              p{
                font: 1.25em/1.375em "Arial";
                font-weight: bold;
              }
              table{
                width: 100%;
                caption{
                  margin: 10px 0;
                  font: .75em/.875em "Arial";
                  text-align: left;
                }
                tr{
                  th{
                    font: .875em/35px "Arial";
                    font-weight: bold;
                    text-align: center;
                    background-color: #ececec;
                    border-top: 1px solid #b6b6b6;
                    border-bottom: 1px solid #b6b6b6;
                  }
                  td{
                    border-top: 1px solid #b6b6b6;
                    border-bottom: 1px solid #b6b6b6;
                    a{
                      width: 100%;
                      display: block;
                      font: .875em/35px "Arial";
                      font-weight: bold;
                      color: #0081ef;
                      text-align: center;
                    }
                    span{
                      width: 100%;
                      display: block;
                      font: .875em/35px "Arial";
                      text-align: center;
                    }
                  }
                }
              }
              .customQuantity{
                ul{
                  margin: 25px 0;
                  display: flex;
                  justify-content: space-between;
                  li{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                  }
                  .quantity{
                    width: 40%;
                    p{
                      font: 1em/35px "Arial";
                      font-weight: bold;
                    }
                    input{
                      width: 65px;
                      height: 35px;
                      font: 1em/35px "Arial";
                      color: #5c5c5c;
                      text-align: center;
                      border: 1px solid #c7c7c7;
                      outline: none;
                    }
                    input::-webkit-outer-spin-button,
                    input::-webkit-inner-spin-button{
                      -webkit-appearance: none;
                    }
                    input[type="number"]{
                      -moz-appearance: textfield;
                    }
                  }
                  .total{
                    width: 55%;
                    p{
                      width: 80px;
                      height: 35px;
                      font: 1em/35px "Arial";
                      color: #fff;
                      text-align: center;
                      background-color: #9e9e9e;
                    }
                    span{
                      font: 1.813em/35px "Arial";
                      font-weight: bold;
                    }
                  }
                }
                .orderInfo{
                  text-align: center;
                  span{
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    &:nth-child(4){
                      font-size: 1.188em;
                    }
                  }
                }
                a{
                  margin: 20px 0;
                  width: 100%;
                  display: block;
                  font: 1.125em/35px "Arial";
                  font-weight: bold;
                  color: #fff;
                  text-align: center;
                  text-decoration: none;
                  background-color: #009fff;
                  &:hover{
                    background-color: #007eec;
                  }
                }
              }
            }
          }
        }
        .col-lg-9{
          .product{
            &__tabs{
              .tabs{
                display: flex;
                li{
                  width: 160px;
                  position: relative;
                  a{
                    width: 160px;
                    display: block;
                    position: relative;
                    font: 1.125em/60px "Arial";
                    font-weight: bold;
                    color: #000;
                    text-align: center;
                    text-decoration: none;
                    &:focus{
                      outline: none;
                    }
                  }
                  &:focus{
                    outline: none;
                  }
                }
                .ui-tabs-active{
                  .ui-tabs-anchor{
                    height: 61px;
                    position: absolute;
                    background-color: #f0f0f0;
                    border-top: 1px solid #b8b8b8;
                    border-right: 1px solid #b8b8b8;
                    border-left: 1px solid #b8b8b8;
                    z-index: 5;
                    outline: none;
                  }
                }
              }
              .tabs-1,
              .tabs-2,
              .tabs-3{
                padding: 30px;
                background-color: #f0f0f0;
                border: 1px solid #b8b8b8;
              }
              .tabs-1,
              .tabs-2{
                p{
                  font: .875em/1.125em "Arial";
                  color: #555;
                }
              }
              .tabs-3{
                ul{
                  margin: 20px 0;
                  li{
                    p{
                      font: .938em/1.063em "Arial";
                      font-weight: bold;
                      color: #555;
                    }
                    span{
                      font: .938em/1.063em "Arial";
                      color: #555;
                    }
                  }
                  &:nth-child(2){
                    li{
                      &:nth-child(1){
                        &:before{
                          content: "";
                          margin: 0;
                        }
                      }
                      &:before{
                        content: "-";
                        margin: 0 0 0 5px;
                        position: relative;
                        left: -5px;
                      }
                    }
                  }
                }
              }
            }
            .social{
              margin: 30px 0;
              display: flex;
              justify-content: space-between;
              align-items: center;
              p{
                font: .813em/.938em "Arial";
                color: #525252;
                span{
                  font-weight: bold;
                }
              }
              ul{
                display: flex;
                li{
                  margin: 0 5px;
                  display: flex;
                  align-items: center;
                  a{
                    padding: 0 5px;
                    display: flex;
                    align-items: center;
                    font: .75em/20px "Arial";
                    color: #fff;
                    text-decoration: none;
                    border-radius: 2.5px;
                    i{
                      font-size: 1.25em;
                      color: #fff;
                    }
                  }
                  .mobile{
                    display: none;
                  }
                  span{
                    margin: 0 0 0 7px;
                    display: block;
                    font-size: .813em;
                    border: 1px solid #c2c2c2;
                    border-radius: 2.5px;
                  }
                  &:nth-child(1){
                    a{
                      background-color: #3667bb;
                    }
                    span{
                      padding: 0 8px;
                      position: relative;
                      &:before{
                        content: "";
                        position: absolute;
                        top: 3px;
                        left: -13px;
                        border: 6px solid transparent;
                        border-right: 7px solid #c2c2c2;
                      }
                      &:after{
                        content: "";
                        position: absolute;
                        top: 3px;
                        left: -12px;
                        border: 6px solid transparent;
                        border-right: 7px solid #fff;
                      }
                    }
                  }
                  &:nth-child(2){
                    a{
                      background-color: #0096e3;
                    }
                  }
                  &:nth-child(3){
                    a{
                      color: #d7051d;
                      border: 1px solid #9b9b9b;
                      box-sizing: border-box;
                      i{
                        color: #d7051d;
                      }
                    }
                  }
                  &:nth-child(4){
                    a{
                      background-color: #ff6d49;
                    }
                    span{
                      padding: 0 13px;
                      position: relative;
                      &:before{
                        content: "";
                        position: absolute;
                        top: 3px;
                        left: -13px;
                        border: 6px solid transparent;
                        border-right: 7px solid #c2c2c2;
                      }
                      &:after{
                        content: "";
                        position: absolute;
                        top: 3px;
                        left: -12px;
                        border: 6px solid transparent;
                        border-right: 7px solid #fff;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .col-lg-9{
          .product{
            &__alsoViewed{
              p{
                font: 1.25em/1.5em "Arial";
                font-weight: bold;
              }
            }
          }
        }
        .col-lg-12{
          .product{
            &__alsoViewed{
              display: flex;
              justify-content: space-between;
              .cart{
                margin: 15px 0;
                width: 23%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
                border: 1px solid #dfdfdf;
                a{
                  display: block;
                  text-decoration: none;
                  img{
                    width: 100%;
                    padding: 15px 20px;
                  }
                  span{
                    display: block;
                    padding: 15px 20px;
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    color: #0081ef;
                  }
                }
                ul{
                  display: flex;
                  li{
                    a{
                      display: block;
                      font: .875em/40px "Arial";
                      font-weight: bold;
                      color: #777;
                      text-align: center;
                      background-color: #eee;
                      border-top: 1px solid #dfdfdf;
                    }
                  }
                  .inCart{
                    width: 62%;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    border-left: 1px solid #dfdfdf;
                  }
                }
                .hover{
                  display: none;
                  width: 262.19px;
                  position: absolute;
                  bottom: -150px;
                  left: -1px;
                  background-color: #fff;
                  border: 1px solid #78afeb;
                  border-top: none;
                  box-shadow: 5px 5px 15px #cecece;
                  z-index: 5;
                  .descr{
                    padding: 10px 20px;
                    display: flex;
                    flex-direction: column;
                    li{
                      p{
                        font: .875em/1.375em "Arial";
                        font-weight: bold;
                        span{
                          padding: 0 0 0 5px;
                          font-weight: normal;
                        }
                      }
                    }
                  }
                  .about{
                    padding: 0 20px 15px;
                    font: .875em/1em "Arial";
                    color: #636363;
                  }
                }
                &:hover{
                  border-color: #78afeb;
                  border-bottom-color: #dfdfdf;
                  box-shadow: 5px 5px 15px #cecece;
                  .hover{
                    display: block;
                  }
                }
              }
            }
          }
          .aside{
            display: none;
          }
        }
      }
    }
  }
  .sliderPopup{
    width: 1260px;
    background: #fff;
    .container{
      .row{
        .col-lg-12{
          p{
            font: 1.375em/1.5em "Arial";
            font-weight: bold;
            text-align: center;
            position: relative;
            #close{
              position: absolute;
              top: -25px;
              right: -35px;
              color: #c0c0c0;
              &:hover{
                cursor: pointer;
              }
            }
          }
          .popupNav{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li{
              margin: 14px;
              width: 65px;
              height: 65px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 1px solid #c5c5c5;
              img{
                padding: 5px;
                width: 100%;
              }
              &:hover{
                cursor: pointer;
              }
            }
            .cycle-pager-active{
              border-color: #009fff;
            }
          }
        }
        .slider{
          display: flex;
          justify-content: space-around;
          align-items: center;
          .popupSlider{
            margin: 50px 0;
            width: 70%;
            display: flex;
            justify-content: center;
            align-items: center;
            li{
              width: 100%;
              text-align: center;
              user-select: none;
              img{
                width: 475px;
              }
            }
          }
          #prev,
          #next{
            width: 15%;
            display: flex;
            justify-content: center;
            align-items: center;
            i{
              font-size: 5em;
              color: #009fff;
              &:hover{
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
}